
.scroll-wrapper {
  overflow: hidden;
  background: #fff;
}
.scroll-content {
  width: 100%;
  z-index: 1;
}
.list-content {
  position: relative;
  z-index: 10;
  background: #fff;
  list-style-type: none;
}
.list-item {
  height: 60px;
  line-height: 60px;
  font-size: 18px;
  padding-left: 20px;
  border-bottom: 1px solid #e5e5e5;
}
.pulldown-wrapper {
  position: absolute;
  z-index: 11;
  width: 100%;
  top: -40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #555;
  left: 0;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-transition: all;
  transition: all;
}
.before-trigger {
  display: inline-block;
}
.pullup-wrapper {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 11px 0;
}

/* 上拉提示信息 */
.bottom-tip {
  width: 100%;
  height: 35px;
  line-height: 35px;
  text-align: center;
  color: #777;
  background: #f2f2f2;
  /* position: absolute;
  bottom: 0px; */
}



