
#search {
  background-color: #f7f7f7;
  width: 100%;
  position: absolute;
  top: 0;
  height: 100%;
}
a {
  color: #000;
}
#search .header_search {
  padding: 9px 0 9px 12px;
  background-color: #fff;
}
.fr {
  float: right;
}
.fl {
  float: left;
}
#search .header_search .back {
  display: block;
  width: 24px;
  height: 27px;
  background: url(../../../assets/back.png) no-repeat left center;
}
#search .left {
  position: relative;
  background: #e6e6e6;
  height: 27px;
  width: 77%;
}
#search .left input {
  margin-left: 8px;
  line-height: 27px;
  /* color: #b3b3b3; */
  font-size: 14px;
  padding-left: 20px;
  background: url(../../../assets/search.png) no-repeat left center;
  border: none;
}
#search .left ul {
  position: absolute;
  top: 0;
  right: 0;
  color: #212121;
  font-size: 14px;
  line-height: 27px;
}
#search .left .sib {
  position: relative;
  background: url(../../../assets/bottom.png) no-repeat;
  background-position: 36px center;
  padding-right: 34px;
}
#search .left .active {
  background: url(../../../assets/top.png) no-repeat;
  background-position: 36px center;
}
#search .left .sib ul {
  position: absolute;
  display: none;
  width: 100%;
  left: 0;
  top: 27px;
  z-index: 1;
  color: #212121;
  font-size: 14px;
  border: 1px solid #eee;
  text-align: center;
  line-height: 27px;
  margin-left: -2px;
}
#search .left .sib ul li {
  border-bottom: 1px dashed #e6e6e6;
}
#search .left .sib ul li:last-child {
  border: none;
}
#search .left .sib .ac {
  display: block;
}
#search .left .sib .sh {
  display: none;
}
#search .right {
  display: block;
  color: #4d4d4d;
  font-size: 14px;
  line-height: 27px;
  margin-right: 12px;
}
#search .search_title {
  margin-top: 16px;
  line-height: 38px;
  font-size: 14px;
  color: #666666;
  margin-left: 12px;
  position: relative;
}
#search .search_title .scimg {
  position: absolute;
  top: 15px;
  right: 15px;
}
#search .hot_search {
  background-color: #fff;
}
#search .hot_search li {
  float: left;
  width: 33.3%;
  line-height: 60px;
  height: 60px;
  text-align: center;
  color: #333333;
  font-size: 14px;
    border-bottom: 1px dashed #e6e6e6;
  border-right: 1px dashed #e6e6e6;
}
#search .hot_search li:nth-child(3n) {
  border-right:none;
}
/* #search .hot_search li:nth-child(1) {
  border-bottom: 1px dashed #e6e6e6;
}
#search .hot_search li:nth-child(2) {
  border-bottom: 1px dashed #e6e6e6;
  border-left: 1px dashed #e6e6e6;
  border-right: 1px dashed #e6e6e6;
}
#search .hot_search li:nth-child(3) {
  border-bottom: 1px dashed #e6e6e6;
}
#search .hot_search li:nth-child(5) {
  border-left: 1px dashed #e6e6e6;
  border-right: 1px dashed #e6e6e6;
} */
#search .lately_search {
  background-color: #fff;
}
#search .lately_search li {
  margin-left: 12px;
  position: relative;
  color: #333333;
  font-size: 14px;
  line-height: 45px;
  border-bottom: 1px dashed #e6e6e6;
}
#search .lately_search li:last-child {
  border-bottom: none;
}
#search .lately_search li p {
  position: absolute;
  top: 0;
  right: 12px;
  width: 20px;
  height: 45px;
  background: url(../../../assets/sc.png) no-repeat center center;
}



