
.bottom{
	position: fixed;
	left: 0;
	bottom: 0;
	height:50px;
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	text-align: center;
	font-size: 10px;
	border-top: 1px solid #e0e0e0;
	background: #fff;
	z-index: 10000000;
}
.bottom .tab-time{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	color: #212121;
}
.bottom .ac{
	color: #e53e41;
}
.bottom .tab-time .time_inner{
	margin-top: 12px;
}
.bottom .tab-time .time_inner .top{
	height:20px;
}
.bottom .tab-time .time_inner .bot{
	line-height:20px;
}
.bottom .tab-time .time_inner_wallet{
	position: relative;
}
.bottom .tab-time .time_inner_wallet .top div{
	position: absolute;
	top: -28px;
    left: 50%;
    margin-left: -24px;
    background: #fff;
    border-top: 1px solid #e0e0e0;
    border-radius: 50%;
}
.bottom .tab-time .time_inner_wallet .top img{
	text-align: center;
	vertical-align: middle;
	padding: 5px;
}



